div{
   font-weight: 800; 
}
.headerRight{
    display: block;
    text-align: right;
    margin: 5px;
}

.icons{
    vertical-align: middle;
    font-weight: lighter;
    padding: 10px;
    text-decoration: none;
    color: #2e393b;
}
.icons:hover{
    text-decoration: underline overline;
}
.BannerImage{
    object-fit: contain;
    height: 90px;
}
.homeBody img{
    display: block;
    margin: 100px  auto 0px auto;
}
/* Home Below buttons */
.homeButtons{
    display: flex;
    justify-content: center;
}
.homeButtons button{
    background-color: rgba(202, 200, 200, 0.247);
    color: rgba(0, 0, 0, 0.726);
    font-size: 14px;
    margin: 0px 5px;
    height: 2rem;
    border-radius: 5px;
    border: 1px solid rgba(128, 128, 128, 0.418);
    padding: 5px 20px;
}
.homeButtons button:hover{
box-shadow: 0px 0px 1px 1px rgba(128, 128, 128, 0.418);
}